
import request from '../utils/request'
import { msg, errorMsg } from '../utils/common'

import { mapState } from 'vuex'
import Panel from 'vux/src/components/panel/index.vue'

import noData from '../components/noData'

export default {
name: 'viewstu',
components: {
Panel,
noData
},
data () {
return {
stuList: [],
panelList: [{
title: '钉钉',
desc: '是打发斯蒂芬',
src: 'https://static.byr.cn',
fallbackSrc: 'https://static.byr.cn/img/logo_footer.gif'

}]
}
},
computed: {
...mapState({
courseId: state => state.course.courseId,
courseName: state => state.course.courseName,
teaOpenid: state => state.user.infoObj.teaOpenid
})
},
mounted () {
this._getStu()
},
methods: {
_getStu () {
request({
url: 'http://www.zhiheyikaoqin.cn/tea/findCandidatesByCourseId?courseId=' + this.courseId + '&teaOpenid=' + this.teaOpenid,
method: 'get'
}).then(res => {
if (res.data.code !== 0) {
this.stuList = []
return
}
this.stuList = res.data.data.map(item => ({
selected: false,
stuOpenid: item.stuOpenid,
stuCode: item.stuCode,
title: item.stuName,
desc: item.stuLevel + ' ' + item.stuGrade + ' ' + item.stuClass,
src: 'df',
fallbackSrc: item.stuHeadimgurl || 'http://img4.imgtn.bdimg.com/it/u=5933999,1764766349&fm=27&gp=0.jpg',
meta: {
source: '信用得分',

other: item.creditScore !== null ? item.creditScore + '分' : '暂无'
}
}))
})
},
selStu (item) {
console.log(item)
this.stuList.map(item => {
item.selected = false
})
request({
url: 'http://www.zhiheyikaoqin.cn/tea/saveSelectedStu',
method: 'post',
data: {
courseId: this.courseId,
stuOpenid: item.stuOpenid
}
}).then(res => {
if (res.data.code === 0) {
this.$set(item, 'selected', true)
msg(this, '选中成功')
} else {
errorMsg(this, res.data.msg)
}
}, 20)
}
}
}



